import React from "react";
import Counter from "./Counter";

class Footer extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const date = new Date();
    const year = date.getFullYear();
    return (
      <footer>
        <p>
          Creado por{" "}
          <a href="https://www.linkedin.com/in/dguzmanmichael/" target="_blank">
            Michael Guzmán
          </a>
        </p>
        <p>
          <a>Todos los derechos reservados {year} ©</a>
        </p>
        <Counter></Counter>
      </footer>
    );
  }
}

export default Footer;
