import React from "react";
import { FaRocket } from "react-icons/fa";
import zakologo from "./imgs/Zakoblue.svg";

class Header extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div id="Header">
        <h1 id="HeaderH1">
          <img src={zakologo} width="30"></img>
          Zako
        </h1>
      </div>
    );
  }
}

export default Header;
